html{
  font-size: 62.5%;
  /*=10px */
 }
 body{
  //line-height: 1.5;
   font-size: 1.6rem;
    /*16px */
  // letter-spacing: 0.5em;
    /*50px */
   box-sizing: border-box;
   //color: $color_default;
 }
 img{
   vertical-align: bottom;
   width: 100%;
 }

 ul {
  list-style: none;
}
li {
  list-style: none;
}

a {
   text-decoration: none;
   transition: .3s;
   //color: $color_default;
 }
 a:hover {
  transition: .3s;
  opacity: .7;
}

input {
  box-sizing: border-box;
}


button:hover {
  transition: .3s;
  opacity: .7;
  cursor: pointer;
}

input,textarea {
  resize: none;
  appearance: none;
  outline: none;
  border: none;
  width: 100%;
  padding: 0;
  background: none;
  border-radius: 0;
  }

select {
    -webkit-appearance: none;
  resize: none;
  outline: none;
  border: none;
  width: 95%;
  height: 100%;
  padding: 5px;
  }

  p {
  //margin-block-start: 0em;
  //margin-block-end: 1em;
  //margin-inline-start: 0px;
  //margin-inline-end: 0px;
  //letter-spacing: 0.5px;
  }
  button{
    background-color: transparent;
    border: none;
    cursor: pointer;
    outline: none;
    padding: 0;
    appearance: none;
}